import React, { useCallback, useState } from 'react'
import { useWeb3Context } from 'web3-react'
import styled from 'styled-components'
import moment from 'moment'
import { makeStyles } from '@material-ui/core/styles'
import Button from '@material-ui/core/Button'
import { useDAOIsOpenVote, useDAOProposalVotes, useDAOVote } from '../hooks/dao'
import {
  useActivityJoin,
  useActivityReward,
} from '../hooks/activity'
import { ACTIVITY_DEMO_ADDRESSES } from '../constants'

const Activity = styled.div`
  flex-direction: column
  background-color: #FFFFFF
  display: flex
  box-shadow: 0 2px 6px #b5b5b5
  border-radius: 8px
  box-sizing: border-box
  margin: 20px 0
`

const Title = styled.div`
  padding: 10px
  font-weight: 600;
  background-color: rgb(238, 237, 244)
`

const Body = styled.div`
  display: flex;
  justify-content: space-between;
  padding: 20px;
`

const Description = styled.div``
const Location = styled.div``
const StartDateTimeBox = styled.div``

const Participants = styled.div`
  display: flex;
  flex-direction: column;
  
  > *:not(:first-child) {
    margin-top: 1rem;
  }
`

const Count = styled.div`
  text-align:center
  font-size:24px
  `
const Voters = styled.div`
  display: flex;
  flex-direction: column;

  > *:not(:first-child) {
    margin-top: 1rem;
  }
`
const IsPassed = styled.div`
  margin-top: 1rem;
`

const LeftBox = styled.div`
  display: flex;
  
  > *:not(:first-child) {
    margin-left: 2rem;
  }
`
const RightBox = styled.div`
  width: 300px;
`
const useStyles = makeStyles({
  button: {
    background: '#2377E3',
    border: 0,
    borderRadius: 20,
    boxShadow: '0 3px 5px 2px rgba(255, 105, 135, .3)',
    color: 'white',
    height: 40,
  },
});


export default function ActivityItem(props) {
  const classes = useStyles()

  const { account, networkId } = useWeb3Context()
  const isConnected = !!account

  const isOpenVote = useDAOIsOpenVote()

  const votes = useDAOProposalVotes(props.id)
  const [participientAmount, setParticipientAmount] = useState(1)

  const startTime = moment(props.startTime * 1000).format(
    'YYYY/MM/DD A hh:mm',
  )

  const vote = useDAOVote()
  const handleVote = useCallback(() => {
    vote(props.id)
  }, [vote, props.id])

  const join = useActivityJoin(props.recipient)
  const handleJoin = useCallback(() => {
    join('')
    setParticipientAmount(participientAmount + 1)
  }, [join, participientAmount])

  const reward = useActivityReward(ACTIVITY_DEMO_ADDRESSES[networkId])
  const handleReward = useCallback(() => {
    reward()
  }, [reward])
  
  return (
    <Activity>
      <Title></Title>
      <Body>
        <LeftBox>
          <Participants>
            <Count>{participientAmount ? participientAmount : 0} </Count>
            {!props.isPassed && (
              <Button
                className={classes.button}
                disabled={!isConnected}
                onClick={handleJoin}
              >
                
              </Button>
            )}
          </Participants>
          <Voters>
            <Count>
            {
              props.isPassed ? 3 : votes ? votes.toNumber() : 0
            } </Count>
            {!props.isPassed && (
              <Button
                className={classes.button}
                disabled={!isConnected || !isOpenVote}
                onClick={handleVote}
              >
                
              </Button>
            )}
          </Voters>
        </LeftBox>
        <RightBox>
          <Description>{': ' + props.description}</Description>
          <Location>{': ' + props.location}</Location>
          <StartDateTimeBox>{': ' + startTime}</StartDateTimeBox>

          {props.isPassed && (
            <IsPassed>
              {
                ('',
                props.isRewarded ? (
                  ''
                ) : (
                  <Button
                    className={classes.button}
                    disabled={!isConnected}
                    onClick={handleReward}
                  >
                    
                  </Button>
                ))
              }
            </IsPassed>
          )}
        </RightBox>
      </Body>
    </Activity>
  )
}
